<template>
  <div>
    <head-bar class="head"></head-bar>

    <foot-bar class="foot"></foot-bar>
  </div>
</template>

<script>
//导入组件
// import mcontent from '../components/mcontent.vue'
import headBar from '../components/head-bar.vue'
import footBar from '../components/foot-bar.vue'

export default {
  components: {
    headBar, footBar
  }
}
</script>

<style scoped>
  .head {
    width: 100%;
    height: 65px;
    background-color: #fff;
    border-bottom: 1px solid #e7e7e7;
    z-index: 999;
    display: flex;
    justify-content: center;
    /*固定定位*/
    position: fixed;
    left: 0;
    top: 0;
  }
  .foot {
    width: 100px;
    height: 100px;
    position: fixed;
    right: 10px;
    bottom: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
</style>





